
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html,
body {
    height: 100%;
    font-family: 'Lato', sans-serif; /* Aplica la fuente globalmente */
}

.app {
    display: flex;
    flex-direction: column; /* Cambia a columna para mejor organización */
    align-items: center;
    justify-content: center;
    padding: 15px;
    height: 100vh; /* Asegura que ocupe toda la altura de la ventana */
}

.logo-contenedor {
    margin-bottom: 20px; /* Espacio inferior */
}

.contenedor-principal {
    height: 550px;
    min-width: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0 4px 10px rgba(130, 231, 222, 0.1); /* Sombra sutil */
    padding: 20px; /* Espaciado interno */
}

.logo {
    height: 150px;
    width: auto; /* Mantiene la proporción del logo */
    border-radius: 15px;
}

button {
    margin: 10px; /* Espacio entre botones */
    padding: 10px 20px; /* Espaciado interno del botón */
    border: none;
    border-radius: 5px; /* Bordes redondeados para los botones */
    background-color: #69ecf3; /* Color de fondo */
    color: white; /* Color del texto */
    cursor: pointer; /* Cambia el cursor al pasar por encima */
    transition: background-color 0.3s; /* Transición suave */
}

button:disabled {
    background-color: #69ecf3; /* Color para el botón deshabilitado */
    cursor: none; /* Cambia el cursor cuando está deshabilitado */
}

button:hover:not(:disabled) {
    background-color: #69ecf3; /* Color al pasar el ratón */
}
