.button-container {
    display: flex; 
    flex-direction: column; /* Alinea los botones verticalmente */
    align-items: center; /* Centra los botones horizontalmente */
    justify-content: center; /* Centra los botones verticalmente */
    height: 100vh; /* Hace que el contenedor tenga la altura completa de la ventana */
}

button{
    width: 600px;
    height: 80px;
    font-size: 35px;
    font-family: Roboto ,sans-serif;
    font-weight: bold;
    color: rgb(22, 90, 96);
    border: 3px solid rgba(8, 146, 159, 0.608);
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
    align-items: center;
    justify-content: center;
   
}


